@import "accueil.sass";


h2, p, h3 {
    @include families($family: "Vidaloka"); }

.detailsText {
    @include couleursBkg($couleurBkg: "vert");
    p {
        @include couleursTexte($couleurTexte: "grisfonce"); } }

h3 {
    @include couleursTexte($couleurTexte: "rose"); }

h2 {
    @include couleursTexte($couleurTexte: "jaune"); }

#myCarousel {
    height: 600px;
    width: auto;
    overflow: hidden; }

/*animation*/
.my-div {
  position: relative;
  height: 50vh;
  width: 100%;
  background: black;
  display: flex;
  box-shadow: 0px 10px 40px rgba(0,0,0,.4); }

canvas {
  position: absolute;
  border-radius: 6px;
  z-index: 0; }

.animText {
   z-index: 99; }
/*fin*/

#retourRT {
  @include boutons($bouton: "app");
  @include ombres($ombre: "bordRose"); }
